<template>
    <div class="views">
        <nav_desktop :nav_planshet="nav_planshet" />
        <home_desktop />
        <div class="btnes">
            <button class="openNav" v-show="btnNav" @click="isNavbar">
               <div class="border"></div>
            </button>
            <button class="close_nav" v-show="btnNav2" @click="closeNavbar">
                <img src="@/assets/img/icons/close.svg" alt="">
            </button>
        </div>
    </div>
</template>

<script>
    import nav_desktop from './components/nav.vue'
    import home_desktop from './components/home.vue'
    export default {
        components: {
            nav_desktop,
            home_desktop
        },
        data() {
            return {
                nav_planshet: "",
                btnNav:true,
                btnNav2:false
            }
        },
        methods: {
            isNavbar() {
                this.btnNav = false
                this.btnNav2 = true
                this.nav_planshet = "active"
            },
            closeNavbar(){
                this.nav_planshet = ''
                this.btnNav = true
                this.btnNav2 = false
                
            }
        }
    }
</script>

